.messages {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scrollbar-width: none;
  }

  .notification-badge {
    position: relative;
    display: inline-block;
    margin-top: 0;
    height: 5px;
    margin-left: 10px;
  }
  
  .badge {
    position: absolute;
    top: 0;
    right: 0;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 0px 5px 0.5px 5px;
  }